.button{
	border-radius: 5px;
	padding:8px 20px;
	cursor: pointer;
	background-color: white;
}

.button_red{
	border:1px solid red;
	color: red;
}
.button_red:hover{
	border:1px solid red;
}

.button_orange{
	border:1px solid orange;
	color: orange;
}
.button_orange:hover{
	border:1px solid orange;
}
.button_blue{
	border:1px solid blue;
	color: blue;
}
.button_blue:hover{
	border:1px solid blue;
}