.main__container{
	background:#fff;
	box-shadow: 0px 8px 11px -8px grey;
	margin-bottom: 15px;
	width:100%;
	padding: 25px 20px;
	border-radius:5px;
}

.items__container{
	display:flex;
	justify-content: space-between;
	gap:10px;
}
.item__container {
	width:100%;
}

.item__container label{
	display:flex;
	flex-direction:column;
	gap:5px;
	font-size:15px;
}
.item__container input{
	font-size: 15px;
	padding: 10px 5px;
	border-radius: 2px;
	border: 0.5px solid black;
}
.button__container{
	width:100%;
	text-align: right;
	margin-top:10px;
}